//
// Syntax highlighting
//
hljs.initHighlightingOnLoad();


//
// Copy to clipboard
//
var clipboard = new Clipboard('.c-snippet__copy');

clipboard.on('success', function(e) {
	$('.c-snippet__copy').attr('aria-label', 'Copied!');
	e.clearSelection();

	$('.c-snippet__copy').on({
		mouseleave: function () {
	        $('.c-snippet__copy').attr('aria-label', 'Copy');
	    }
	});
});

$('.c-toc').singlePageNav({
	currentClass: 'is-active',
	offset: 30,
	speed: 500
});

clipboard.on('error', function(e) {
	alert('There is something wrong with clipboard, Copy the code manually');
});